<template>
    <div class="needHome">
        <div class="select">
              <span>筛选:</span>
              <span>行业领域</span>
          <el-select v-model="value1" placeholder="全部" size="small">
            <el-option
              v-for="item in industry"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
         <span>合作方式</span>
          <el-select v-model="value2" placeholder="专利许可" size="small">
            <el-option
              v-for="item in patent"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span>需求类别</span>
          <el-select v-model="value3" placeholder="技术需求" size="small">
            <el-option
              v-for="item in technology"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-button type="primary" size="small">查询</el-button>
        </div>
        <div class="needWarp">
             <div>
                  <ul>
                    <li>行业领域</li>
                    <li>需求概述</li>
                    <li>需求类别</li>
                    <li>合作方式</li>
                    <li>预投金额</li>
                    <li>截止时间</li>
                    <li>操作列</li>
                  </ul>
                  <ul>
                 <li>制造业</li>
                 <li>需要对锂电池回收节约30%能耗及其他</li>
                 <li>生产需求</li>
                 <li>专利许可</li>
                 <li>30万</li>
                 <li>2019-9-10</li>
                 <li style="color: rgba(3, 87, 255, 0.749019607843137)"><span @click="toDetail" style="cursor: pointer">查看详情</span>&nbsp;&nbsp;<span>留言</span></li>
               </ul>
               <ul>
                 <li>制造业</li>
                 <li>需要对锂电池回收节约30%能耗及其他</li>
                 <li>生产需求</li>
                 <li>专利许可</li>
                 <li>30万</li>
                 <li>2019-9-10</li>
                 <li style="color: rgba(3, 87, 255, 0.749019607843137)"><span @click="toDetail" style="cursor: pointer">查看详情</span>&nbsp;&nbsp;<span>留言</span></li>
               </ul>
               <ul>
                 <li>制造业</li>
                 <li>需要对锂电池回收节约30%能耗及其他</li>
                 <li>生产需求</li>
                 <li>专利许可</li>
                 <li>30万</li>
                 <li>2019-9-10</li>
                 <li style="color: rgba(3, 87, 255, 0.749019607843137)"><span @click="toDetail" style="cursor: pointer">查看详情</span>&nbsp;&nbsp;<span>留言</span></li>
               </ul>
             </div>
        </div>
        <div class="details" v-if="ifDetails" v-drag>
          <Details @fathermethod="closeDetails"></Details>
        </div>
      <div style="width: 1104px;margin: 0 auto;text-align: right;margin-top: 50px">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="100">
        </el-pagination>
      </div>
    </div>
</template>

<script>
  import Details from './Details'
    export default {
        name: "NeedHome",
        data(){
            return {
                industry: [{
                    value: '选项1',
                    label: '智能设备'
                }, {
                    value: '选项2',
                    label: '化工'
                }, {
                    value: '选项3',
                    label: '新材料'
                }, {
                    value: '选项4',
                    label: '轻工'
                }],
                patent: [{
                    value: '选项1',
                    label: '专利许可'
                }, {
                    value: '选项2',
                    label: '委托开发'
                }, {
                    value: '选项3',
                    label: '技术入股'
                }],
                technology: [{
                    value: '选项1',
                    label: '技术需求'
                }, {
                    value: '选项2',
                    label: '其他'
                }, {
                    value: '选项3',
                    label: '人才需求'
                }, {
                    value: '选项4',
                    label: '人才需求'
                    }],
                value1: '',
                value2:'',
                value3:'',
                ifDetails:false
            }
        },
        components:{
            Details
        },
        methods:{
            closeDetails(){
                this.ifDetails = false;
            },
            toDetail(){
                this.ifDetails =true;
            }
        },
        directives: {
            //拖拽&&缩放指令
            drag: {
                bind: (el, binding) => {
                    console.log(el);
                    console.log(binding.value);
                    //绑定默认样式
                    el.style.zIndex = 9;
                    //如果为编辑状态
                    if (binding.value || binding.value === undefined) {
                        //定义该元素的 top left width height
                        let x, y, w, h;
                        //鼠标的起始和结束坐标
                        let cx_start, cy_start, cx_end, cy_end;
                        //判断鼠标样式
                        el.onmousemove = e => {
                            //获取鼠标当前位置
                            let cx_now = e.clientX;
                            let cy_now = e.clientY;
                            //获取div右下角相对浏览器的位置
                            let {
                                top: el_top,
                                left: el_left,
                                width: el_width,
                                height: el_height
                            } = el.getBoundingClientRect();
                            let el_bottom_height = el_top + el_height;
                            let el_right_width = el_left + el_width;
                            //判断鼠标是否在div下边界
                            let mouse_in_bottom =
                                cy_now <= el_bottom_height + 5 && cy_now >= el_bottom_height - 5;
                            //判断鼠标是否在div右边界
                            let mouse_in_right =
                                cx_now <= el_right_width + 5 && cx_now >= el_right_width - 5;
                            if (mouse_in_bottom && mouse_in_right) {
                                el.style.cursor = "se-resize";
                            } else if (mouse_in_right) {
                                el.style.cursor = "e-resize";
                            } else if (mouse_in_bottom) {
                                el.style.cursor = "s-resize";
                            } else {
                                el.style.cursor = "move";
                            }
                        };
                        el.onmousedown = e => {
                            let mouse = el.style.cursor;
                            //更改默认样式
                            el.style.zIndex = 99;
                            //对象解构赋值
                            let {left: el_x, top: el_y, width: el_w, height: el_h} = window.getComputedStyle(el);
                            x = el_x;
                            y = el_y;
                            w = el_w;
                            h = el_h;
                            console.log(x, y, w, h)
                            cx_start = e.clientX;
                            cy_start = e.clientY;
                            //绑定移动事件
                            document.onmousemove = e => {
                                cx_end = e.clientX;
                                cy_end = e.clientY;
                                //默认左下方向配置
                                let x_move = cx_end - cx_start;
                                let y_move = cy_end - cy_start;
                                let direct = ["width", "height"];
                                let pos = [w, h];
                                let move = [x_move, y_move];
                                let limit = 50;
                                //判断鼠标的类型进行对应的操作
                                switch (mouse) {
                                    case "e-resize":
                                        direct = ["width"];
                                        pos = [w];
                                        move = [x_move];
                                        break;
                                    case "s-resize":
                                        direct = ["height"];
                                        pos = [h];
                                        move = [y_move];
                                        break;
                                    case "move":
                                        direct = ["left", "top"];
                                        pos = [x, y];
                                        limit = 0;
                                        break;
                                }
                                handle_div(direct, pos, move, limit);
                            };
                            //取消移动事件
                            document.onmouseup = e => {
                                //还原默认样式
                                el.style.zIndex = 9;
                                document.onmousemove = null;
                            };

                            /**
                             * 操作DOM位置和大小方法
                             * @param direct 方向
                             * @param pos 尺寸/坐标
                             * @param move 拖动距离
                             * @param limit 限定范围
                             */
                            function handle_div(direct, pos, move, limit) {
                                for (let i = 0; i < direct.length; i++) {
                                    let val = parseInt(pos[i]) + move[i];
                                    val = val <= limit ? limit : val;
                                    el.style[direct[i]] = val + "px";
                                }
                            }
                        };
                    } else {
                        el.style.cursor = "default";
                        //移除点击事件
                        el.onmousedown = null;
                        el.onmousemove = null;
                    }
                }
            }
        }
    }
</script>

<style scoped lang="less">
  .select {
     height: 60px;
     line-height: 60px;
    background-color: rgba(3, 87, 255, 0.749019607843137);
    text-align: center;
    color: #FFFFFF;
  }
  .needWarp {
     div {
       ul {
         text-align: center;
         height: 40px;
         line-height: 40px;
         li {
           display: inline-block;
           font-size: 14px;
           color: #333333;
           height: 42px;
         }
         li:nth-child(1){
           width: 117px;
         }
         li:nth-child(2){
           width: 283px;
         }
         li:nth-child(3){
           width: 108px;
         }
         li:nth-child(4){
           width: 108px;
         }
         li:nth-child(5){
           width: 126px;
         }
         li:nth-child(6){
           width: 165px;
         }
         li:nth-child(7){
           width: 120px;
         }
       }
     }
  }
  .details {
      position: absolute;
      width: 691px;
      height: 1200px;
      border: 1px solid rgba(121, 121, 121, 1);
      top: 176px;
      left: 50%;
      transform: translateX(-50%);
      background-color: rgba(255, 255, 255, 1);
  }
</style>



