<template>
    <div class="needForm">
        <div class="title">
            企业需求发布登记
        </div>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="*需求类别">
          <el-radio-group v-model="form.resource">
            <el-radio label="1、技术需求"></el-radio>
            <el-radio label="2、人才需求"></el-radio>
            <el-radio label="3、生产需求"></el-radio>
            <el-radio label="4、其他"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="*合作方式">
          <el-radio-group v-model="form.resource">、
            <div style="margin-bottom: 10px">
              <el-radio label="1、技术转让"></el-radio>
              <el-radio label="2、专利许可"></el-radio>
              <el-radio label="3、委托开发"></el-radio>
            </div>
            <div>
              <el-radio label="4、合作开发"></el-radio>
              <el-radio label="5、技术服务"></el-radio>
              <el-radio label="6、技术入股"></el-radio>
              <el-radio label="7、其他"></el-radio>
            </div>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="*需求行业">
          <el-select v-model="form.region" placeholder="请选择">
            <el-option label="装备制造" value="shanghai"></el-option>
            <el-option label="轻工" value="beijing"></el-option>
            <el-option label="其他" value="beijing"></el-option>
          </el-select>
          <el-select v-model="form.region" placeholder="请选择">
            <el-option label="轻工" value="shanghai"></el-option>
            <el-option label="装备制造" value="beijing"></el-option>
            <el-option label="其他" value="beijing"></el-option>
            <el-option label="智能材料" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="*需求概述">
          <el-input v-model="form.Summary"></el-input>
        </el-form-item>
        <el-form-item label="*需求详情(限500字内)">
          <el-input type="textarea" v-model="form.desc" :autosize="{ minRows: 6}" maxlength="500"  show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="*期望实现结果">
          <el-input v-model="form.result"></el-input>
        </el-form-item>
        <el-form-item label="*需求企业名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="*所属市县">
          <el-cascader
            size="large"
            :options="options"
            v-model="selectedOptions"
            @change="handleChange">
          </el-cascader>
        </el-form-item>
        <el-form-item label="*联系人">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="*电话">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="*邮箱">
          <el-input v-model="form.mail"></el-input>
        </el-form-item>
        <el-form-item label="*需求截止时间">
          <el-col :span="11">
            <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="备注" >
          <el-input type="textarea" v-model="form.desc" :autosize="{ minRows: 6}"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">立即创建</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
    import { regionData } from 'element-china-area-data'
        export default {
        name: "NeedRelease",
        data() {
            return {
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    phone:'',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: '',
                    mail:'',
                    result:'',
                    Summary:''
                },
                options: regionData,
                selectedOptions: []
            }
        },
        methods: {
            onSubmit() {
                console.log('submit!');
            },
            handleChange (value) {
                console.log(value)
            }
        }
    }
</script>

<style  lang="less">
   .el-form-item__content {
     margin-left: 170px!important;
   }
   .el-form-item__label {
     width: 150px!important;
   }
   .needForm {
     width: 685px;
     margin: 0 auto;
     .title {
          font-size: 18px;
          color: #333333;
          margin-top: 76px;
          margin-bottom: 56px;
          font-weight: 600;
          text-align: center;
     }
   }
  .needWarp {
    text-align: center;
    height: 860px;
    vertical-align: top;
    .warpL {
      width: 150px;
      height: 100%;
      display: inline-block;
      background-color: green;
       ul {
         li {
           list-style: none;
         }
       }
    }
    .warpR {
      width: 490px;
      height: 100%;
      display: inline-block;
      background-color: blue;
      vertical-align: top;
       ul {
         li {
           text-align: left;
           list-style: none;
         }
       }
    }
  }
</style>



