<template>
  <div class="need">
         <div class="need-head">
               <div class="tec-logo">
                 <img src="../assets/tec-logo.png" alt="">
               </div>
               <span class="need-head-1">政企智慧服务平台>企业科技需求征集系统</span>
               <span class="need-head-2">欢迎您&nbsp;&nbsp;!&nbsp;&nbsp;王*德</span>
               <span class="need-head-3">退出</span>
         </div>
         <div class="tabs">
          <ul>
            <li><span :class="$route.path.indexOf('NeedHome') >= 0 ?'active':''" @click="toTabs(1)" style="cursor: pointer">需求大厅</span></li>
            <li><span :class="$route.path.indexOf('NeedDetail') >= 0 ?'active':''" @click="toTabs(2)" style="cursor: pointer">需求概述</span></li>
            <li><span :class="$route.path.indexOf('NeedRelease') >= 0 ?'active':''"   @click="toTabs(3)" style="cursor: pointer">发布需求</span></li>
          </ul>
         </div>
          <router-view></router-view>
          <foot></foot>
  </div>
</template>

<script>
  import foot from "./footer";
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '',
    }
  },
  methods:{
      toTabs(i){
          console.log(i)
          if (i==1){
              this.$router.push('/Home/NeedHome')
          }
          if (i==2){
              this.$router.push('/Home/NeedDetail')
          }
          if (i==3){
              this.$router.push('/Home/NeedRelease')
          }
      }
  },
    components:{
        foot
    }
}
</script>
<style lang="less" scoped >
     .need {
       width: 100%;
       min-width: 1024px;
     }
     .need-head {
       box-sizing: border-box;
       width: 100%;
       height: 60px;
       line-height: 60px;
       background-color: rgba(3, 87, 255, 0.745098039215686);
       padding-left: 70px;
     }
     .tec-logo {
       display: inline-block;
     }
     .need-head-1 {
       font-size: 18px;
       font-weight: 600;
       color: #ffffff;
     }
     .need-head-3 {
       color: #fff;
       font-weight: 400;
       font-size: 14px;
       position: absolute;
       right: 24px;

     }
     .need-head-2 {
       color: #fff;
       font-weight: 400;
       font-size: 14px;
       float: right;
       padding-right: 68px;
     }
  .tabs {
    height: 45px;
    background-color: rgba(255, 255, 255, 0.749019607843137);
    text-align: center;
    line-height: 45px;
    color: rgba(3, 87, 255, 0.749019607843137);
    font-size: 16px;
    font-weight: 650;
    border-bottom: 1px solid rgba(188, 188, 188, 1);
    font-family: 'PingFangSC-Semibold', 'PingFang SC Semibold', 'PingFang SC';
  }
  .tabs ul li {
    list-style: none;
    display: inline-block;
    width: 173px;
  }
  .tabs ul li span {
    display: inline-block;
    height: 100%;
    padding-left: 8px;
    padding-right: 8px;
  }
     .tec-logo {
   position: absolute;
    left: 30px;
    top: 9px;
  }
  .active {
    background-color: rgba(3, 87, 255, 0.749019607843137);
    color: #FFFFFF;
  }
</style>



